import {Component} from 'react';
import styles from './Searchbar.module.scss'


class Searchbar extends Component {
    
    // dadawda =(e)=>{
    //     e.preventDefault();
    //     console.log(111);
    //     const key = '21709969-2aaf655592c1caf292dc08cd8';
    //     const numberPAge = 1;
    //     const url=`https://pixabay.com/api/?q=что_искать&page=${numberPAge}&key=${key}&image_type=photo&orientation=horizontal&per_page=12` 
    //     console.log(url);
    // }
    render(){
        return(
            <header className={styles.Searchbar}>
                <form className={styles.SearchForm}>
                    <button type="submit" className={styles.SearchForm__button}>
                        <span className={styles.SearchForm__button__label}>Search</span>
                    </button>

                    <input
                        className={styles.SearchForm__input}
                        type="text"
                        autocomplete="off"
                        autofocus
                        placeholder="Search images and photos"
                    />
                </form>
            </header>
        )
    }
}

export default Searchbar;